.container {
    display: flex;
    justify-content: center;
    position: relative;
    margin-top: 20px;
}

.heart {
    position: absolute;
    width: 400px;
    height: 340px;
    z-index: 2;
}

.heart::before, .heart::after {
    content: "";
    position: absolute;
    top: 350px;
    width: 208px;
    height: 320px;
    border-radius: 200px 200px 0 0;
    background: red;
}

.heart::before {
    left: 200px;
    transform: rotate(-45deg);
    transform-origin: 0 100%;
}

.heart::after {
    left: -8px;
    transform: rotate(45deg);
    transform-origin: 100% 100%;
}

